
var Helper = require('Helper');

cc.Class({
	extends: cc.Component,

	properties: {
		page:    cc.Prefab,
		content: cc.Node,
		date:    cc.Label,
		changerDate: 0,
	},
	onLoad: function(){
		let page = cc.instantiate(this.page);
		page.y = -341;
		this.node.addChild(page);
		this.page = page.getComponent('Pagination');
		this.page.init(this);

		Promise.all(this.content.children.map(function(obj){
			return obj.getComponent('xsmb_cuoc_item');
		}))
		.then(tab => {
			this.content = tab;
		});
		let date = new Date();
		this.date.string = Helper.numberPad(date.getDate(), 2) + '/' + Helper.numberPad(date.getMonth()+1, 2) + '/' + date.getFullYear();
	},
	onEnable: function() {
		this.get_data();
	},
	get_data: function(page = 1){
		cc.RedT.send({xs:{mb:{history:{date:this.date.string, page:page}}}});
	},
	onData: function(data) {
		var self = this;
		this.page.onSet(data.page, data.kmess, data.total);
		this.content.forEach(function(obj, i){
			let dataT = data.data[i];
			if (void 0 !== dataT) {
				obj.node.active   = true;
				obj.bg.active     = i%2;
				obj.time.string   = Helper.getStringDateByTime(dataT.time);
				obj.nick.string   = dataT.name;
				obj.loai.string   = self.getLoai(dataT.type);
				obj.so.string     = dataT.so.join(', ');
				obj.diem.string   = Helper.numberWithCommas(dataT.diem);
				obj.cuoc.string   = Helper.numberWithCommas(dataT.cuoc);
				obj.win.string    = Helper.numberWithCommas(dataT.win);
			}else{
				obj.node.active = false;
			}
		});
	},
	dateNext: function(){
		this.changerDate++;
		this.dateChanger();
	},
	datePre: function(){
		this.changerDate--;
		this.dateChanger();
	},
	dateChanger: function(){
		let date = new Date();
		date.setDate(date.getDate()+this.changerDate);
		this.date.string = Helper.numberPad(date.getDate(), 2) + '/' + Helper.numberPad(date.getMonth()+1, 2) + '/' + date.getFullYear();
		this.get_data();
	},
	getLoai: function(data){
		switch(data){
			case 'lo2':
				return 'L 2 S'
				break;
			case 'lo21k':
				return 'L 2 S 1k'
				break;
			case 'lo3':
				return 'L 3 S'
				break;
			case 'lo4':
				return 'L 4 S'
				break;
			case 'xien2':
				return 'Xin 2'
				break;
			case 'xien3':
				return 'Xin 3'
				break;
			case 'xien4':
				return 'Xin 4'
				break;
			case 'de':
				return ''
				break;
			case 'daude':
				return 'u '
				break;
			case 'degiai7':
				return ' Gii 7'
				break;
			case 'degiai1':
				return ' Gii Nht'
				break;
			case '3cang':
				return '3 Cng'
				break;
			case '4cang':
				return '4 Cng'
				break;
			case 'dau':
				return 'u'
				break;
			case 'duoi':
				return 'ui'
				break;
			case 'truot4':
				return 'Trt 4'
				break;
			case 'truot8':
				return 'Trt 8'
				break;
			case 'truot10':
				return 'Trt 10'
				break;
		}
	},
});
